body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fallback */
@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/materialicons/v47/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-font-feature-settings: 'liga';
    -moz-osx-font-smoothing: grayscale;
}

body {
    text-align: center;
    padding: 0;
    margin: 0;
    overflow: scroll !important;
}

a {
    cursor: pointer;
}


.level-one-objects-grid img {
    object-fit: cover;
    object-position: top;
    width: 100%;
    max-width: 250px;
    max-height: 110px;
    cursor: pointer;
}

.level-one-objects-grid .row {
    display: flex;
    justify-content: center;
}

.level-one-objects-grid .row .col.s4 {
    display: flex;
    margin-left: 0;
    justify-content: center;
}

.level-one-objects-grid .image-grid {
    margin-top: 2em;
}

.level-one-objects-grid .selected {
    border: 3px solid teal;
    opacity: 0.7;
}


@media screen and (min-width: 1200px) {
    .level-one-objects-grid .row .col.s4 {
        width: 26%;
    }
}

@media screen and (min-width: 600px) {
    .level-one-objects-grid img {
        max-height: 250px;
    }
}
.col.s4.level-one-object {
    position: relative;
}

i.material-icons.small {
    position: absolute;
    right: 11%;
    color: teal;
}
@media screen and (min-width: 600px) {
    .help-page-help-container .row {
        margin-top: 30px;
    }
}

.help-page-help-container button {
    margin-top: 20px;
}


.level-two-objects {
    margin-top: 10px;
}

.level-two-objects img {
    object-fit: cover;
    object-position: top;
    height: 150px;
    width: 150px;
}

.images-droppable img {
    margin: 0 5px;
}

.level-two-objects .col.s6.images {
    text-align: left;
}

h6 {
    margin-bottom: 10px;
}

.placeholder img {
    cursor: pointer;
}

.modal-content img {
    float: left;
    margin-right: 20px;
    max-height: 300px;
    max-width: 200px;
}

.placeholder {
    height: 155px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: #b5b5b5;
}

.descriptions-collection {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}

.description {
    border: 1px solid #e0e0e0;
    padding: 0.5em;
    height: 155px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.correct {
    border: 1px solid #009688;
}

.description i.small {
    font-size: 1.2rem;
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    color: #009688;
}

@media screen and (min-width: 600px) {
    .level-two-objects img {
        max-height: 250px;
    }

    .level-two-objects .descriptions-collection {
        max-width: 300px;
        margin-left: auto;
        margin-right: 0;
    }
}
.select-wrapper {
    max-width: 300px;
    margin: 10px auto;
    color: white;
}

.select-dropdown.dropdown-trigger {
    color: white;
}

.select-wrapper input.select-dropdown {
    border-bottom: 1px solid white;
}

.select-dropdown.dropdown-content li.selected {
    background-color: white !important;
}

.select-wrapper .caret {
    fill: white;
}

.select-dropdown.dropdown-content li:hover {
    background-color: #eeeeee !important;
}

.choose-language {
    margin-top: 40px;
}

.landing-page-text {
    padding: 20px;
    background-color: #009688;
    max-width: 80%;
    margin: 2.2em auto;
    color: #fff;
}

.landing-page-text h1 {
    font-weight: 600;
    font-size: 3.2rem;
}

.landing-page-text a {
    color: #58c5fa;
    font-weight: 600;
    font-size: 1.40rem;
}

.landing-page {
    background-image: url(/boring-things-quiz/static/media/titlepage.924c6b45.jpg);
    min-height: 100vh;
    background-size: cover;
}

.landing-page-text h6 {
    line-height: 1.6;
    font-weight: 500;
}

@media screen and (min-width: 992px) {
    .landing-page-text {
        margin-top: 4.5em;
    }
}

@media screen and (min-width: 1200px) {
    .landing-page-text {
        width: 60%;
        font-size: 4.2rem;
        padding: 20px 40px;
        margin-top: 2em;
    }

    .landing-page {
        min-height: 100vh;
    }

    .landing-page h6 {
        font-size: 1.3rem;
    }
}







/*.select-dropdown.dropdown-content li.selected:hover {*/
    /*background-color: #eeeeee !important;*/
/*}*/
.restart-page h6 {
    line-height: 1.6;
}

.restart-page {
    padding: 20px;
}

.restart-page img {
    max-height: 400px;
    margin-top: 20px;
}

.restart-page h5 {
    font-size: 1.2rem;
    line-height: 1.5;
}

@media screen and (min-width: 990px) {
    .restart-page h5 {
        font-size: 1.5rem
    }

    .restart-page {
        padding: 20px 15%;
    }

}

@media screen and (min-width: 600px) {

}
.level-four img {
    max-height: 400px;
    margin-top: 20px;
    cursor: pointer;
    max-width: 70%;
}

.reactions img {
    height: 70px;
    width: auto;
    cursor: pointer;
    max-width: 100%;
}

.reactions {
    display: flex;
    justify-content: center;
    margin-bottom: 5px;
}

.cta {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.cta .btn {
    margin: 10px 20px 5px;
}

.cta .start-again {
    order: 2;
    margin-bottom: 15px;
}

.cta .next-object {
    order: 1;
}

.error {
    color: #e51c23;
}

@media screen and (min-width: 600px) {
    .cta .start-again {
        order: 1;
        margin-bottom: 0;
    }

    .cta .next-object {
        order: 2;
    }

    .reactions {
        margin-bottom: 20px;
    }
}

.reactions div {
    margin: 0 30px;
}



.breadcrumb:last-child {
    color: rgba(255,255,255,0.7);
}

.breadcrumb.active {
    color: #fff;
}

.breadcrumb::before {
    margin-right: 0 !important;
    margin-left: 0;
}

